import { library } from "@fortawesome/fontawesome-svg-core";

// ------ Solid Icons ------- //
import {
    faBookmark,
    faVideo,
    faHome,
    faFilm,
    faTv,
    faUser,
    faSearch,
    faThumbsUp,
    faThumbsDown,
    faTrashAlt,
    faChevronDown,
    faSignOutAlt,
    faLock,
    faFrownOpen,
} from "@fortawesome/free-solid-svg-icons";

// ------ Regular Icons ------ //
import {
    faBookmark as regularBookmark,
    faThumbsUp as regularThumsUp,
    faThumbsDown as regulatThumbsDown,
} from "@fortawesome/free-regular-svg-icons";

export const initializeFontIcons = () => {
    library.add(
        faBookmark,
        faVideo,
        faHome,
        faFilm,
        faTv,
        faUser,
        faSearch,
        regularBookmark,
        faThumbsDown,
        faThumbsUp,
        regularThumsUp,
        regulatThumbsDown,
        faTrashAlt,
        faChevronDown,
        faSignOutAlt,
        faLock,
        faFrownOpen
    );
};
